@font-face {
  font-family: "KievitPro";
  src: local("KievitPro-Regular"), local("KievitPro Regular"),
    url("../fonts/KievitPro-Regular.woff2") format("woff2"),
    url("../fonts/KievitPro-Regular.woff") format("woff");
  font-weight: 400;
  font-style: normal;
  font-display: swap;
}

